<!--
 * @Descripttion: 自定义列通用组件
 * @version:
 * @Author: ZJM
 * @Date: 2020-04-01 09:44:42
 * @LastEditors: ZJM
 * @LastEditTime: 2020-08-13 10:34:21
 -->

<template >
  <div class="content">
    <!-- 左侧固定列 -->
    <vue-draggable
      class="draggable"
      animation="380"
      ghostClass="ghost"
      v-model="headColumn"
      handle=".move-row"
    >
      <li v-for="item in headColumn" class="node-row" :key="item.field">
        <el-checkbox :disabled="true" :checked="item.visible"></el-checkbox>
        <div class="headProess move-row">
          <div class="title move-row">
            {{item.title}}
            <i icon="node-move" class="icon-button"></i>
          </div>
          <div class="proess" :style="'width:'+item.width"></div>
        </div>
        <el-input v-model="item.width">
          <template slot="append">px</template>
        </el-input>
      </li>
    </vue-draggable>

    <!-- 中间移动列 -->
    <vue-draggable
      class="draggable"
      animation="380"
      ghostClass="ghost"
      v-model="middleColumn"
      handle=".move-row"
    >
      <li v-for="item in middleColumn" class="node-row move-row" :key="item.field">
        <el-checkbox :checked="item.visible" @change="checkChange(item)"></el-checkbox>
        <div class="headProess move-row">
          <div class="title">
            {{item.title}}
            <i icon="node-move" class="icon-button"></i>
          </div>
          <div class="proess" :style="'width:'+item.width"></div>
        </div>
        <el-input v-model="item.width">
          <template slot="append">px</template>
        </el-input>
      </li>
    </vue-draggable>

    <!-- 右侧侧固定列 -->
    <vue-draggable
      class="draggable"
      animation="380"
      ghostClass="ghost"
      v-model="operationColumn"
      handle=".move-row"
    >
      <li v-for="item in operationColumn" class="node-row" :key="item.field">
        <el-checkbox :disabled="true" :checked="item.visible"></el-checkbox>
        <div class="headProess move-row">
          <div class="title">
            {{item.title}}
            <i icon="node-move" class="icon-button"></i>
          </div>
          <div class="proess" :style="'width:'+item.width"></div>
        </div>
        <el-input v-model="item.width">
          <template slot="append">px</template>
        </el-input>
      </li>
    </vue-draggable>
    <div slot="footer" class="dialog-footer">
      <el-button type="primary" @click="saveCustomColumn">{{BTN_TEXT.CONFIRM}}</el-button>
      <el-button @click="canleCustomColumn">{{BTN_TEXT.CLOSE}}</el-button>
      <el-button @click="resetCustomColumn">{{BTN_TEXT.RESTORE_DEFAULT}}</el-button>
    </div>
  </div>
</template>

<script>
import { BTN_TEXT } from '@/utils/constantsFile';
// 拖拽列表组件
import VueDraggable from 'vuedraggable';
export default {
  components: {
    VueDraggable
  },
  props: {
    nowColumns: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      first: false,
      BTN_TEXT: BTN_TEXT,
      checkColumn: [],
      headColumn: [],
      middleColumn: [],
      operationColumn: [],
      columns: [],
      oldColumns: [],
      width: []
    };
  },
  computed: {},
  created: function() {
    this.$nextTick(() => {
      this.oldColumns = this.deepClone(this.nowColumns);
      this.checkColumn = [];
      this.headColumn = [];
      this.middleColumn = [];
      this.operationColumn = [];
      this.columns = [];
      for (var i = 0; i < this.nowColumns.length; i++) {
        if (this.nowColumns[i].type !== 'checkbox') {
          if (this.nowColumns[i].visible === undefined) {
            this.nowColumns[i].visible = true;
          }
          var json = {};
          if (this.nowColumns[i].width === undefined) {
            this.nowColumns[i].width = 'auto';
            json.width = 'auto';
            json.field = this.nowColumns[i].field;
          } else {
            json.width = this.nowColumns[i].width;
            json.field = this.nowColumns[i].field;
          }
          this.width.push(json);
          if (this.nowColumns[i].fixed === 'left') {
            this.headColumn.push(this.deepClone(this.nowColumns[i]));
          } else if (this.nowColumns[i].fixed === 'right') {
            this.operationColumn.push(this.deepClone(this.nowColumns[i]));
          } else {
            this.middleColumn.push(this.deepClone(this.nowColumns[i]));
          }
        } else {
          this.checkColumn.push(this.deepClone(this.nowColumns[i]));
        }
      }
    });
  },
  methods: {
    init(arr) {
      this.$nextTick(() => {
        this.checkColumn = [];
        this.headColumn = [];
        this.middleColumn = [];
        this.operationColumn = [];
        this.columns = [];
        setTimeout(() => {
          for (var i = 0; i < arr.length; i++) {
            if (arr[i].type !== 'checkbox') {
              if (arr[i].visible === undefined) {
                arr[i].visible = true;
              }
              for (var item in this.width) {
                if (this.width[item].field === arr[i].field) {
                  arr[i].width = this.width[item].width;
                }
              }
              if (arr[i].fixed === 'left') {
                this.headColumn.push(arr[i]);
              } else if (arr[i].fixed === 'right') {
                this.operationColumn.push(arr[i]);
              } else {
                this.middleColumn.push(arr[i]);
              }
            } else {
              this.checkColumn.push(arr[i]);
            }
          }
        }, 0);
      });
    },
    /**
     * @description: 保存设置
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    saveCustomColumn() {
      this.columns = [];
      this.columns = this.columns
        .concat(this.checkColumn)
        .concat(this.headColumn)
        .concat(this.middleColumn)
        .concat(this.operationColumn);
      setTimeout(() => {
        this.oldColumns = this.deepClone(this.columns);
        this.width = [];
        for (var item in this.columns) {
          var json = {};
          json.width = this.nowColumns[item].width;
          json.field = this.nowColumns[item].field;
          this.width.push(json);
        }
        this.$emit('saveCustomColumn', this.columns);
      }, 150);
    },
    deepClone(obj) {
      const objClone = Array.isArray(obj) ? [] : {};
      if (obj && typeof obj === 'object') {
        for (var key in obj) {
          // eslint-disable-next-line no-prototype-builtins
          if (obj.hasOwnProperty(key)) {
            // 判断ojb子元素是否为对象，如果是，递归复制
            if (obj[key] && typeof obj[key] === 'object') {
              objClone[key] = this.deepClone(obj[key]);
            } else {
              // 如果不是，简单复制
              objClone[key] = obj[key];
            }
          }
        }
      }
      return objClone;
    },
    /**
     * @description: 选中框事件
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    checkChange(item) {
      item.visible = !item.visible;
    },
    /**
     * @description: 取消
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    canleCustomColumn() {
      this.$emit('showCustomColumn');
    },
    /**
     * @description: 恢复默认
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    resetCustomColumn() {
      this.$emit('resetCustomColumn');
    }
  }
};
</script>

<style lang="less"  scoped>
// * {
//   box-sizing: border-box;
// }
.content {
  overflow: auto;
}
li {
  list-style: none;
  height: 31px;
}
.el-input-group {
  width: 115px;
  margin-left: 2px;
}
.el-input-group /deep/ .el-input__inner {
  height: 25px;
  border-radius: 0px;
}
.el-input-group /deep/ .el-input-group__append {
  border-radius: 0px;
}

.headProess {
  width: 400px;
  height: 25px;
  position: relative;
  display: inline-block;
  border: 1px solid #ddd;
  vertical-align: middle;
  margin-left: 2px;
}
.title {
  position: absolute;
  height: 25px;
  line-height: 25px;
  padding-left: 10px;
  top: 0;
}
.proess {
  background: #ddd;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
}
.el-checkbox /deep/ .el-checkbox__inner {
  width: 25px;
  height: 25px;
}
.el-checkbox /deep/ .el-checkbox__inner::after {
  height: 15px;
  left: 8px;
  width: 8px;
}
.draggable {
  margin-bottom: 15px;
}
</style>
