// 引入标题组件
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
// 引入标题元素组件
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
// 引入复杂搜索
// import ComplexQuery from '@/components/common/PageNavBar/ComplexQuery';
// 引入table组件
import VXETable from '@/components/common/Table';
// 引入通用方法
// import { CommonService } from '../../../../services/common.service';
// 引入警告信息
import { showWarningMessage, showSuccessMessage } from '@/utils/message';
// 引入错误提示
import { showErrorNotify } from '@/utils/notify';
// 搜索框
import SearchBox from '@/components/common/SearchBox';
import CustomColumn from '@/components/common/CustomColumn';
import { SYSTEM_CONFIG } from '@/utils/ajaxConfigure';
// 文字常量
import {
  VARIABLE,
  EQUIPMENT_DATA,
  BTN_TEXT,
  MSG,
  ENERGY,
  PAGE_TITLE,
  TITLE,
  FIXTASK_STATUS,
  FIXTASK_TYPE_STATUS,
  STATUS_CODE,
  EXPORT_NAME,
  REPAIR_TASK,
  CONSTANTS_DEVICE_CONFIGURATION
} from '@/utils/constantsFile';
// 导入所需接口
import {
  getUnReviewCheckRecord,
  checkReview,
  checkUnReview
} from '@/api/checkConfig';
import ImportDialog from '@/components/common/ImportDialog';
import { searchCondetionFunc } from '@/utils/publicFunction';
export default {
  name: 'FixTask',
  computed: {},
  components: {
    HeaderTitle,
    HeadOperationItem,
    VXETable,
    SearchBox,
    ImportDialog,
    CustomColumn
  },
  inject: ['reload'],
  data() {
    return {
      REPAIR_TASK: REPAIR_TASK,
      VARIABLE: VARIABLE,
      EXPORT_NAME: EXPORT_NAME,
      FIXTASK_TYPE_STATUS: FIXTASK_TYPE_STATUS,
      TITLE: TITLE,
      PAGE_TITLE: PAGE_TITLE,
      ENERGY: ENERGY,
      MSG: MSG,
      FIXTASK_STATUS: FIXTASK_STATUS,
      EQUIPMENT_DATA: EQUIPMENT_DATA,
      BTN_TEXT: BTN_TEXT,
      isImportDialogShow: false,
      // 维修类型管理表
      manageTaskTable: {
        loading: false,
        data: [],
        selectRecords: [],
        isAllChecked: false,
        isIndeterminate: false,
        page: {
          total: 0,
          currentPage: 1,
          pageSize: 10,
          pageSizes: [5, 10, 20, 50, 100, 200, 500],
          perfect: true,
          slots: { left: 'pager_left' },
          layouts: [
            'Sizes',
            'PrevJump',
            'PrevPage',
            'Number',
            'NextPage',
            'NextJump',
            'FullJump',
            'Total'
          ]
        },
        validRules: {
          repairName: [
            {
              required: true,
              message: MSG.PLEASE_ENTER_CONTENT
            },
            { max: 100, message: '长度不得大于100个字符' },
            { pattern: /^[A-Za-z0-9\u4e00-\u9fa5]+$/, message: '不允许输入空格等特殊符号' }
          ],
          typeIdList: [
            { required: true, message: MSG.PLEASE_CHOOSE_CONTENT, trigger: 'change' }
          ]
        },
        checkFlag: false
      },
      checkboxConfig: { checkField: 'checked', trigger: 'row' },
      // 维修任务表数据
      fixTableData: {
        data: [],
        page: {
          total: 0,
          currentPage: 1,
          pageSize: 10,
          align: 'right',
          pageSizes: [5, 10, 20, 50, 100, 200, 500],
          perfect: true,
          slots: { left: 'pager_left' },
          layouts: [
            'Sizes',
            'PrevJump',
            'PrevPage',
            'Number',
            'NextPage',
            'NextJump',
            'FullJump',
            'Total'
          ]
        },
        columns: []
      },
      active2: 2,
      tab: [],
      // 车间列表
      groupFilterData: null,
      // 维修/故障类型列表
      repairTypeFilterData: null,
      // 设备名称列表
      deviceNameList: null,
      // 用户名列表
      userNameList: null,
      // 初始化过滤数据
      filterData: {
        group: null,
        divice: null,
        repairType: null,
        user: null,
        repairtState: null
      },
      // 控制表格载入效果
      tableLoading: false,
      // 页面标题
      title: '',
      // 表中选中的数据
      selectRecords: [],
      // 默认页面标题
      defaultPageTitle: PAGE_TITLE.REPAIR_TASK,
      // 创建维修单模态框数据
      createNewFixTaskModalData: {
        title: BTN_TEXT.CREATE_FIXTASK,
        show: false
      },
      // 确认维修模态框数据
      fixTaskConfirmTaskModalData: {
        title: EQUIPMENT_DATA.REPAIR_CONFIRM,
        show: false,
        data: null,
        readonly: false
      },
      // 维修类型管理页面数据
      fixManagementPage: {
        show: false,
        title: EQUIPMENT_DATA.REPAIR_MANAGEMENT_TYPE,
        data: null,
        renderData: null,
        deleteList: [],
        isSearchBoxShow: false,
        searchList: [],
        searchData: [],
        orderByData: {}
      },
      // 设备快照页面
      equipmentPicPage: {
        show: false,
        title: BTN_TEXT.EQUIPMENT_PIC,
        data: null
      },
      // 当前选中的任务快照数据
      equipmentPicPagedata: null,
      // 查询字段
      searchList: [],
      orderByData: {},
      searchData: [],
      isSearchBoxShow: false,
      // 控制自定义列模态框显示
      dialogVisibleCustomColumn: false,

      // 表头前部分
      tableColumns_head: [],
      // 表头操作部分
      tableColumns_operation: [],
      // 扩展字段数据集
      extendPropertyValueVoList: [],

      // 表格列配置选项
      tableColumns: [
        { type: 'checkbox', width: '50', align: 'center' },
        {
          field: 'recordId',
          minWidth: '120',
          visible: true,
          title: '记录id',
          align: 'center'
        },
        {
          field: 'deviceName',
          minWidth: '120',
          visible: true,
          title: ENERGY.DEVICENAME,
          align: 'center'
        },
        {
          field: 'devicePriCode',
          visible: true,
          title: CONSTANTS_DEVICE_CONFIGURATION.DEVICE_PRCODE,
          minWidth: '200',
          align: 'center'
        },
        {
          field: 'number',
          visible: true,
          title: CONSTANTS_DEVICE_CONFIGURATION.DEVICE_EXTEND,
          minWidth: '200',
          align: 'center'
        },
        {
          field: 'shiftName',
          minWidth: '360',
          visible: true,
          title: '班次时间',
          align: 'center',
          formatter: ({ row }) => {
            return row.shiftName + ':' + row.checkStartTime + ' - ' + row.checkEndTime;
          }
        },
        {
          field: 'checkState',
          minWidth: '100',
          visible: true,
          title: '点检状态',
          align: 'center',
          formatter: ({ row }) => {
            if (row.checkState === 1) {
              return '正常点检';
            } else if (row.checkState === 2) {
              return '点检不合格';
            } else if (row.checkState === 3) {
              return '停机免检';
            } else if (row.checkState === 4) {
              return '假日免检';
            } else if (row.checkState === 5) {
              return '超时点检';
            }
          }
        },
        {
          field: 'checkUser',
          minWidth: '100',
          visible: true,
          title: '点检人',
          align: 'center'
        },
        {
          field: 'checkTime',
          minWidth: '160',
          visible: true,
          title: '点检时间',
          align: 'center'
        }
      ],
      // 保存搜索内输入的搜索条件，防止翻页时变回无条件查询
      savedSearchCondition: [],
      pickerOptions: {
        shortcuts: [
          {
            text: BTN_TEXT.LAST_WEEK,
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          },
          {
            text: BTN_TEXT.LAST_MONTH,
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          },
          {
            text: BTN_TEXT.LAST_THREE_MONTHS,
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }
        ]
      },
      dateTime: '',
      centerDialogVisible: false,
      CHECK_IMG: false
    };
  },
  methods: {
    // 按钮触发事件
    buttonClickEvent(type) {
      if (type === 'exit') {
        this.closeFixManagement();
        return;
      }
      this[`${type}`]();
    },

    /**
     * @description: 获取所有维修任务
     * @param: void
     * @return: void
     */
    getFixTableData() {
      console.log(this.dateTime);
      this.tableLoading = true;

      var conditionJson = {
        conditionJson1: [],
        operatorGroup: 1,
        conditionJson2: []
      };
      if (this.savedSearchCondition.length > 0) {
        conditionJson = searchCondetionFunc(...this.savedSearchCondition);
      }

      conditionJson.conditionJson1.push({
        column: 'checkStartTime',
        type: 'between',
        ptype: 'between',
        value: this.dateTime[0] + ',' + this.dateTime[1]
      });

      if (this.CHECK_IMG) {
        conditionJson.conditionJson1.push({
          column: 'checkState',
          type: 'eq',
          ptype: 'int',
          value: 5
        });
      } else {
        conditionJson.conditionJson1.push({
          column: 'checkState',
          type: 'eq',
          ptype: 'int',
          value: 1
        });
      }

      conditionJson.conditionJson1.push({
        column: 'reviewedBy',
        type: 'isNull',
        ptype: 'int'
      });

      conditionJson.current = this.fixTableData.page.currentPage;
      conditionJson.size = this.fixTableData.page.pageSize;

      getUnReviewCheckRecord({}, conditionJson)
        .then((res) => {
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            this.fixTableData.data = res.rows;
            this.fixTableData.page.total = res.total;
            this.tableLoading = false;
          } else {
            showWarningMessage(res.message);
          }
        })
        .catch((err) => {
          showErrorNotify(err);
        });
    },

    /**
     * 刷新table
     */
    refreshTable() {
      this.fixTableData.page.currentPage = 1;
      this.getFixTableData();
    },
    /**
     * @description: 刷新表格数据
     * @param 无
     * @return void
     * @author: cyb
     */
    refreshTableData(savedSearchCondition) {
      if (savedSearchCondition) {
        this.savedSearchCondition = [...savedSearchCondition];
        this.fixTableData.page.currentPage = 1;
      }
      this.getFixTableData();
    },
    checkboxChangeEvent(records) {
      this.selectRecords = records;
    },
    clickRows(rows) {
      var data = this.$refs.FixGrid.getCheckboxRecords();
      console.log(data);
      var flag = true;
      for (var i = 0; i < data.length; i++) {
        if (data[i].recordId === rows.recordId) {
          flag = false;
        }
      }
      this.$refs.FixGrid.setCheckboxRow(rows, flag);
    },
    handleFixTablePageChange(currentPage, pageSize) {
      this.fixTableData.page.currentPage = currentPage;
      this.fixTableData.page.pageSize = pageSize;
      this.getFixTableData();
    },
    exportTable() {
      console.log(this.dateTime);
      this.tableLoading = true;

      var conditionJson = {
        conditionJson1: [],
        operatorGroup: 1,
        conditionJson2: []
      };
      if (this.savedSearchCondition.length > 0) {
        conditionJson = searchCondetionFunc(...this.savedSearchCondition);
      }

      conditionJson.conditionJson1.push({
        column: 'checkStartTime',
        type: 'between',
        ptype: 'between',
        value: this.dateTime[0] + ',' + this.dateTime[1]
      });
      if (this.CHECK_IMG) {
        conditionJson.conditionJson1.push({
          column: 'checkState',
          type: 'eq',
          ptype: 'int',
          value: 5
        });
      } else {
        conditionJson.conditionJson1.push({
          column: 'checkState',
          type: 'eq',
          ptype: 'int',
          value: 1
        });
      }
      conditionJson.conditionJson1.push({
        column: 'reviewedBy',
        type: 'isNull',
        ptype: 'int'
      });

      conditionJson.current = 1;
      conditionJson.size = 99999999;

      getUnReviewCheckRecord({}, conditionJson)
        .then((res) => {
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            this.$refs.FixGrid.exportDatax('点检审核', res.rows);
            this.tableLoading = false;
          } else {
            showWarningMessage(res.message);
          }
        })
        .catch((err) => {
          showErrorNotify(err);
        });
    },
    showReview() {
      var data = this.$refs.FixGrid.getCheckboxRecords();
      if (data.length === 0) {
        showWarningMessage('请勾选要审核的点检任务');
      } else {
        this.centerDialogVisible = true;
      }
    },
    async checkReviewPass() {
      var data = this.$refs.FixGrid.getCheckboxRecords();
      console.log(666, data);
      var arr = [];
      for (var i = 0; i < data.length; i++) {
        arr.push(data[i].recordId);
      }

      console.log(data, arr);

      try {
        const data = await checkReview({}, arr);
        if (data.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.centerDialogVisible = false;
          showSuccessMessage(data.message);
          this.getFixTableData();
        } else {
          showWarningMessage(data.message);
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    async checkReviewNoPass() {
      var data = this.$refs.FixGrid.getCheckboxRecords();
      var arr = [];
      for (var i = 0; i < data.length; i++) {
        arr.push(data[i].recordId);
      }
      console.log(data, arr);
      try {
        const data = await checkUnReview({}, arr);
        if (data.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.centerDialogVisible = false;
          showSuccessMessage(data.message);
          this.getFixTableData();
        } else {
          showWarningMessage(data.message);
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    }
  },
  mounted() {
    if (SYSTEM_CONFIG[localStorage.getItem('companyId')]) {
      this.CHECK_IMG = SYSTEM_CONFIG[localStorage.getItem('companyId')].CHECK_IMG;
    }

    const end = new Date();
    const start = new Date();
    start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
    this.dateTime = [
      this.$utils.toDateString(start, 'yyyy-MM-dd') + ' 00:00:00',
      this.$utils.toDateString(end, 'yyyy-MM-dd') + ' 23:59:59'
    ];
    this.getFixTableData();
  },
  created() {

  }
};
