 -->

<template >
  <el-dialog :class="[$store.state.dialogMarginClass, 'import-dialog']" :title="importDialogName" :visible.sync="isShow">
    <span class="display-flex">
      <el-link class="width-120 margin-right-10" :underline="false" @click="exportTemplate">{{ exportFileName }}</el-link>
      <el-input size="small" class="margin-right-10" v-model="importFileName" disabled></el-input>
      <el-upload action class="display-flex file-upload" :auto-upload="false" accept=".xlsx, .xls" ref="upload"
        :http-request="submitFile" :file-list="importFiles" :show-file-list="false" :on-change="selectFile"
        :on-success="handleUploadSuccess" :on-error="handleUploadError">
        <el-button size="small" slot="trigger" class="margin-right-10">{{ BTN_TEXT.CHOOSE_FILE }}</el-button>
        <el-button size="small" type="primary" class="custom-button" @click="uploadBtnClick"
          :disabled="importFiles.length === 0">{{ BTN_TEXT.IMPORT }}</el-button>
        <el-button size="small" type="danger" class="custom-button" @click="cleanFile">清空</el-button>
      </el-upload>
    </span>
    <span class="display-flex margin-top-15" v-show="isUploadProgressShow">
      <span>{{ VARIABLE.UPLOAD_PROGRESS }}:</span>
      <el-progress :text-inside="true" :percentage="uploadPercent" :stroke-width="22" :format="formatText"
        :color="uploadProgressColor"></el-progress>
    </span>
    <div class="errorDiv" v-if="errorList.length > 0">
      <div class="errorTitle">{{ VARIABLE.ERROR_INFO }}:{{ errorList.length }}{{ VARIABLE.ARTICLE }}</div>
      <div class="errorListDiv">
        <el-alert :title="item.failData + '  ' + item.failReason" v-for="(item, index) in errorList" :key="index"
          type="error" show-icon></el-alert>
      </div>
    </div>
  </el-dialog>
</template>

<script>
import { BTN_TEXT, VARIABLE, MSG } from '@/utils/constantsFile';
import { showSuccessMessage, showErrorMessage } from '@/utils/message';
export default {
  components: {},
  props: {
    importDialogName: {
      type: String,
      default: ''
    },
    exportFileName: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      BTN_TEXT: BTN_TEXT,
      VARIABLE: VARIABLE,
      importFileName: '',
      importFiles: [],
      // 上传进度条进度值
      uploadPercent: 0,
      // 进度条是否显示
      isUploadProgressShow: false,
      uploadProgressColor: '#0871dc',
      errorList: [],
      isShow: false,
      limit: 1
      // rows: ''
    };
  },
  computed: {},
  created() { },
  methods: {
    exportTemplate() {
      this.$emit('exportTemplate');
    },
    /**
     * @description: （“导入用户”对话框）点击“导入”按钮，上传文件
     * @param 无
     * @return: void
     * @author: ZJM
     */
    submitFile(param) {
      this.isUploadProgressShow = false;
      this.uploadPercent = 0;
      this.errorList = [];
      this.$emit('submitImportFile', param);
      // this.cleanFile();
    },
    /**
     * @description: （“导入用户”对话框）点击“选择文件”按钮，选择需要上传的文件
     * @param {object} file 选中文件的信息
     * @return: void
     * @author: ZJM
     */
    selectFile(file, fileList) {
      // 将选中文件的名称写入input
      this.importFileName = file.name;
      this.importFiles = fileList;
    },
    /**
     * @description: （导入用户对话框）文件上传成功的回调函数
     * @param 无
     * @return: void
     * @author: ZJM
     */
    handleUploadSuccess(res) {
      // this.cleanFile();
      if (res.rows && res.rows.length > 0) {
        this.isUploadProgressShow = false;
        this.uploadPercent = 0;
        // this.cleanFile();
        // this.resetUploadProgressBar();
        // // this.resetUploadProgressBar();
        // // this.isImportDialogShow = false;
        showErrorMessage(MSG.IMPORT_ERROR);
        if (res.rows) {
          this.errorList = res.rows;
        }
      } else {
        // this.cleanFile();
        this.isShow = false;
        console.log('成功', res);
        showSuccessMessage(MSG.IMPORT_SUCCESS);
        // 刷新表格
        this.$emit('closeImportDialog');
      }
    },
    /**
     * @description: （导入用户对话框）文件上传失败的回调函数
     * @param 无
     * @return: void
     * @author: ZJM
     */
    // eslint-disable-next-line handle-callback-err
    handleUploadError(err) {
      // this.cleanFile();
      console.log('失败', err);
      this.isUploadProgressShow = false;
      this.uploadPercent = 0;
      // this.cleanFile();
      // this.resetUploadProgressBar();
      // // this.resetUploadProgressBar();
      // // this.isImportDialogShow = false;
      showErrorMessage(err);
      // console.log(err.rows);
      if (err.rows) {
        this.errorList = err.rows;
        console.log(this.errorList);
        // this.cleanFile();
      }
    },
    /**
     * @description: 点击“导入”按钮，提交文件
     * @param 无
     * @return: void
     * @author: ZJM
     */
    uploadBtnClick() {
      this.$refs.upload.submit();
      this.uploadPercent = 0;
      // this.cleanFile();
      // this.isUploadProgressShow = true;
    },
    /**
     * @description: 格式化进度条显示的文字
     * @param {number} percentage 进度百分比
     * @return: void
     * @author: ZJM
     */
    formatText(percentage) {
      return `${percentage}%`;
    },
    /**
     * @description: 重置进度条
     * @param 无
     * @return: void
     * @author: ZJM
     */
    resetUploadProgressBar() {
      this.isUploadProgressShow = false;
      this.uploadPercent = 0;
      // this.$refs.upload.clearFiles();
      this.$nextTick(() => {
        this.importFileName = '';
      });
      // 清空文件列表
      this.importFileName = '';
      this.errorList = [];
    },
    cleanFile() {
      var that = this;
      this.$nextTick(() => {
        that.importFileName = '';
        that.importFiles = [];
        that.$refs.upload.clearFiles();
        that.errorList = [];
        that.isUploadProgressShow = false;
        that.uploadPercent = 0;
      });
    },
    cleanFile2() {
      var that = this;
      this.$nextTick(() => {
        that.importFileName = '';
        that.importFiles = [];
        that.$refs.upload.clearFiles();
      });
    }
  }
};
</script>

<style lang="less"  scoped>
.width-120 {
  min-width: 120px;
}

.margin-right-10 {
  margin-right: 10px;
}

.margin-top-15 {
  margin-top: 15px;
  display: block;
}

.import-dialog {
  /deep/ .el-link--inner {
    line-height: 32px;
    text-decoration: underline;
  }

  /deep/ .el-input--small {
    max-width: 200px;
  }

  /deep/ .el-dialog__header {
    padding: 10px 10px !important;
  }

  // 进度条样式
  /deep/ .el-progress-bar__outer {
    width: 100%;
  }

  /deep/ .file-upload {
    width: 250px;
    display: inline-block;
  }
}

.errorTitle {
  padding: 0;
  margin: 0;
  height: 40px;
  line-height: 40px;
  font-weight: bold;
  font-size: 20px;
  margin-top: 10px;
}

.el-alert {
  min-height: 25px;
  margin-bottom: 5px;
}

.el-alert /deep/ .el-alert__content {
  min-height: 35px;
  /* line-height: 29px; */
  display: contents;
  /* padding-left: 10px; */
  margin-left: 10px;
}

.el-alert /deep/ .el-alert__title {
  font-size: 13px;
  line-height: 20px;
  text-align: left;
}

.el-alert /deep/ .el-alert__icon {
  font-size: 16px;
  margin-right: 10px;
  width: 16px;
}

.errorListDiv {
  padding: 5px;
  max-height: 300px;
  overflow: auto;
}
</style>
